import React, { ChangeEvent, KeyboardEvent, memo, useState } from 'react';
//import { ButtonComponent } from "../ButtonComponent/ButtonComponent";
import { IconButton, TextField } from "@mui/material";
import styled from "./AddItemForm.module.scss"
import AddTaskIcon from '@mui/icons-material/AddTask';


type AddItemFormType = {
  disabled?: boolean
  addTask: (inputValue: string) => void //   useCallback  -   !!!!!!!!!!
  //    2 
}

//      React.memo(   )
export const AddItemForm: React.FC<AddItemFormType> = memo(({ disabled = false, addTask }: AddItemFormType) => {
  console.log("AddItemForm")

  let [error, setError] = useState<string | null>(null)
  let [inputValue, setInputValue] = useState<string>("")


  const addItemHandler = (taskName: string) => {
    if (/[a-z-]/i.test(taskName)) {
      addTask(taskName);
      setInputValue("");
    } else {
      setError('Required')
    }
  }

  const onChangeHandler = (e: ChangeEvent<HTMLInputElement>) => {
    setInputValue(e.currentTarget.value); //     
  }

  const onKeyDownHandler = (e: KeyboardEvent<HTMLInputElement>) => {
    if (error !== null) setError(null);
    if (e.key === "Enter") addItemHandler(inputValue)
  }

  return (
    <div className={styled.addItemForm}>
      <TextField type="text" label="Type here...."
        value={inputValue}
        onChange={onChangeHandler}
        onKeyDown={onKeyDownHandler}
        variant={"outlined"}
        error={!!error}
        helperText={error}
        size="small"
        disabled={disabled}
      />

      <IconButton
        onClick={() => addItemHandler(inputValue)} disabled={disabled}>
        <AddTaskIcon fontSize="small" color={disabled ? "disabled" : "success"} />
      </IconButton>
    </div >
  )
})

